import React from 'react';
import Slider from 'react-slick';

import slide1 from '../../Assets/images/slider-image-1.jpeg';
import slide2 from '../../Assets/images/slider-image-2.jpeg';
import slide3 from '../../Assets/images/slider-image-3.jpeg';
import blog1 from '../../Assets/images/grocery-banner.png';
import blog2 from '../../Assets/images/grocery-banner-2.jpeg';

export default function MainSlider() {

    let settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 3000,
        arrows: false
    };

    return <>

        <div className="row my-3 pb-2 gx-0">
            <div className="col-md-9">
                <Slider {...settings}>
                    <img height={500} src={slide3} alt="slider for home page 3" className='w-100' />
                    <img height={500} src={slide2} alt="slider for home page 2" className='w-100' />
                    <img height={500} src={slide1} alt="slider for home page 1" className='w-100' />
                </Slider>
            </div>
            <div className="col-md-3">
                <img height={250} src={blog1} alt="blog for home page 1" className='w-100' />
                <img height={250} src={blog2} alt="blog for home page 2" className='w-100' />

            </div>
        </div>
    </>
}
