import React, { useContext } from 'react';
import Style from './Cart.module.css';
import { CartContext } from '../../Context/CartContext.js';
import { useQuery } from 'react-query';
import { BallTriangle } from 'react-loader-spinner';
import toast from 'react-hot-toast';
import { Helmet } from 'react-helmet';
import { Link } from 'react-router-dom';

export default function Cart() {

    let { getLoggedUserCart, removeCartItem, updateProductQuantity, clearCartItem, setNumOfCartItems } = useContext(CartContext)

    async function getUserCart() {

        return await getLoggedUserCart();
    }

    async function removeItem(cartId) {

        let response = await removeCartItem(cartId)
        setNumOfCartItems(response?.data?.numOfCartItems);

        return await removeCartItem(cartId) && await refetch({
            refetchPage: getUserCart,
        }) && toast.success("Removed successfully")
    }

    async function updateCount(productId, count) {

        return await updateProductQuantity(productId, count) && await refetch({
            refetchPage: getUserCart,
        }) && toast.success("updated successfully")
    }

    async function clearCart() {

        return await clearCartItem() && await refetch({
            refetchPage: getUserCart,
        }) && toast.success("Cleared successfully")
    }

    let { data, isLoading, refetch } = useQuery('getUserCart', getUserCart)


    let cart = data?.data;

    return <>

        <Helmet>
            <meta name='description' content='Cart Page' charSet="utf-8" />
            <title>Shopping Cart</title>
        </Helmet>

        {
            isLoading ?
                <div className="w-100 vh-100 py-5 d-flex justify-content-center align-items-center">
                    <BallTriangle
                        height={200}
                        width={100}
                        radius={5}
                        color="#4fa94d"
                        ariaLabel="ball-triangle-loading"
                        wrapperStyle={{}}
                        wrapperClass=""
                        visible={true}
                    />
                </div>

                :

                cart ?

                    <div className="container px-4 mb-5 my-3 mx-auto p-3 bg-main-light">
                        <h3 className='fw-bold'>Sopping Cart:</h3>
                        <h4 className='fw-bolder h6'>Cart Items : <span className='text-main'>{cart.numOfCartItems}</span></h4>
                        <h4 className='fw-bolder h6 text-main mb-4'>Total Cart Price : {cart.data.totalCartPrice} EGP</h4>


                        {cart.data.products.map((product) => <div key={product.product.id} className='row product align-items-center border-bottom border-2 py-2'>

                            <div className="col-md-1 mb-2">
                                <img src={product.product.imageCover} alt={product.title} className='w-100' />
                            </div>

                            <div className="col-md-11">
                                <div className="d-flex justify-content-between align-items-center">

                                    <div className='w-50'>
                                        <h3 className='h6'>{product.product.title.slice(' ').split(0, 3).join(' ')}</h3>
                                        <h6 className='text-main'>Price : {product.price} EGP</h6>

                                    </div>

                                    <div className='fw-bolder me-1'>
                                        <button onClick={() => updateCount(product.product.id, product.count + 1)} className='btn bord-main p-2'>+</button>
                                        <span className='mx-2'>{product.count}</span>
                                        <button onClick={() => updateCount(product.product.id, product.count - 1)} className='btn bord-main p-2'>-</button>
                                    </div>

                                </div>

                                <button onClick={() => removeItem(product.product.id)} className='btn border-danger border-1 p-1 text-danger'><i className='text-danger font-sm fas fa-trash-can'></i> Remove</button>

                            </div>

                        </div>)}

                        <div className="col-md-10 mx-auto d-flex justify-content-between align-items-center mb-2 mt-4">
                            <button onClick={() => clearCart()} className='btn btn-danger text-white col-md-5  my-3'>
                                <i className='font-sm fas fa-trash-can me-2'></i>
                                Clear cart
                            </button>

                            <Link to={'/checkout'} className={`btn bg-main text-white col-md-5 ms-2 my-3 ${Style.ok}`}>
                                <i className='fas fa-shopping-cart fa-1x me-2'></i>
                                Check Out
                            </Link>
                        </div>

                    </div>

                    :
                    <div className="container px-4 my-3 mx-auto p-3 bg-main-light">
                        <h3 className='fw-bold'>Sopping Cart:</h3>
                        <h4 className='fw-bolder h6'>Cart Items : <span className='text-main'>0</span></h4>
                        <h4 className='fw-bolder h6 text-main mb-4'>Total Cart Price : 0 EGP</h4>

                    </div>

        }

    </>
}
