import React from 'react';
import order from '../../Assets/images/order.webp'
import { Link } from 'react-router-dom';

export default function AllOrders() {
    return <>

        <div className="container">
            <div className="row justify-content-center mx-auto text-center">
                <div className="col-md-12 mx-auto">
                    <img src={order} height={500} className='w-100' alt="" />
                </div>

                <Link to={'/products'}><button className='btn bg-main text-white fw-bold w-75 my-3 py-2'>Do you want to browse more products?</button></Link>
            </div>

        </div>

    </>
}
