import React, { useContext } from 'react';
import { CartContext } from '../../Context/CartContext.js';
import { useQuery } from 'react-query';
import { BallTriangle } from 'react-loader-spinner';
import { Helmet } from 'react-helmet';
import toast from 'react-hot-toast';

export default function Wishlist() {

    let { getLoggedUserWishlist, addToCart, setNumOfCartItems, removeWishlistItem, setNumOfWishlistItems } = useContext(CartContext)


    async function removeItem(productId) {

        let response = await removeWishlistItem(productId)
        setNumOfWishlistItems(response?.data?.data?.length)

        return await removeWishlistItem(productId) && await refetch({
            refetchPage: getUserWishlist,
        }) && toast.success("UnLike successfully")

    }

    async function getUserWishlist() {

        return await getLoggedUserWishlist();
    }

    let { data, isLoading, refetch } = useQuery('getUserWishlist', getUserWishlist)


    async function addProductToCart(productId, name) {

        let response = await addToCart(productId)

        if (response?.data?.status === 'success') {
            setNumOfCartItems(response?.data.numOfCartItems)

            let result = await removeWishlistItem(productId)
            setNumOfWishlistItems(result?.data?.data?.length)

            await removeWishlistItem(productId) && await refetch({
                refetchPage: getUserWishlist,
            })

            toast.success(`${name} added To cart`)
        } else {
            toast.error(`${name} Error adding product`)
        }
    }

    let wishList = data?.data;


    return <>

        <Helmet>
            <meta name='description' content='wish list Page' charSet="utf-8" />
            <title>Shopping WishList</title>
        </Helmet>

        {
            isLoading ?
                <div className="w-100 vh-100 py-5 d-flex justify-content-center align-items-center">
                    <BallTriangle
                        height={200}
                        width={100}
                        radius={5}
                        color="#4fa94d"
                        ariaLabel="ball-triangle-loading"
                        wrapperStyle={{}}
                        wrapperClass=""
                        visible={true}
                    />
                </div>

                :

                wishList ?

                    <div className="container-sm px-4 col-md-12 mb-5 my-4 mx-auto p-3 bg-main-light">
                        <h3 className='fw-bold'>Wish List Cart:</h3>
                        <h4 className='fw-bolder h5 mt-2 mb-4'>Number of Items : <span className='text-main'>{wishList.count}</span></h4>

                        {wishList.data.map((product) => < div key={product._id} className='row product align-items-center border-bottom border-2 py-3' >

                            <div className="col-md-2 mb-2">
                                <img src={product.imageCover} alt={product.title} className='w-100' />
                            </div>

                            <div className="col-md-10">
                                <div className="d-flex justify-content-between align-items-center mb-2">

                                    <div>
                                        <h3 className='h6 fw-bold'>{product.title.slice(' ').split(0, 3).join(' ')}</h3>
                                        <h6 className='text-main fw-bold'>Price : {product.price} EGP</h6>
                                    </div>

                                </div>

                                <button onClick={() => removeItem(product._id)} className='btn border-danger border-1 p-1 text-danger fw-bold'><i className="text-danger fa-xl fa-solid fa-heart-crack"></i> UnLike</button>

                                <div className='fw-bolder'>
                                    <button onClick={() => addProductToCart(product._id, product.title.split(' ').slice(0, 2).join(' '))} className='btn bg-main text-white w-100 my-3'>
                                        + Add To Cart
                                        <i className='fas fa-shopping-cart fa-1x ms-2'></i>
                                    </button>
                                </div>

                            </div>

                        </div>)}
                    </div >

                    :
                    <div className="container px-4 my-3 mx-auto p-3 bg-main-light">
                        <h3 className='fw-bold'>Wish List Cart:</h3>
                        <h4 className='fw-bolder h5 mt-2 mb-4'>Number of Items : <span className='text-main'>{wishList.count}</span></h4>

                    </div>

        }

    </>
}
