import axios from "axios";
import { createContext, useContext, useEffect, useState } from "react";
import { UserContext } from "./UserContext.js";

export let CartContext = createContext()



export default function CartContextProvider(props) {

    let headers = {}
    let { userToken } = useContext(UserContext)

    if ((localStorage.getItem('userToken') === userToken) !== null) {

        headers = {
            token: localStorage.getItem('userToken')
        }

    } else {
        headers = {}
    }

    function addToCart(productId) {

        return axios.post(`https://ecommerce.routemisr.com/api/v1/cart`, {

            productId: productId
        }, {
            headers: headers
        }).then((response) => response)
            .catch((error) => error)
    }

    function getLoggedUserCart() {

        return axios.get(`https://ecommerce.routemisr.com/api/v1/cart`, {
            headers: headers
        }).then((response) => response)
            .catch((error) => error)
    }

    function addToWishlist(productId) {

        return axios.post(`https://ecommerce.routemisr.com/api/v1/wishlist`, {

            productId: productId
        }, {
            headers: headers
        }).then((response) => response)
            .catch((error) => error)
    }

    function getLoggedUserWishlist() {

        return axios.get(`https://ecommerce.routemisr.com/api/v1/wishlist`, {
            headers: headers
        }).then((response) => response)
            .catch((error) => error)
    }

    function removeWishlistItem(productId) {

        return axios.delete(`https://ecommerce.routemisr.com/api/v1/wishlist/${productId}`, {
            headers: headers
        }).then((response) => response)
            .catch((error) => error)
    }

    function removeCartItem(cartId) {

        return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/${cartId}`, {
            headers: headers
        }).then((response) => response)
            .catch((error) => error)
    }

    function clearCartItem() {

        return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart`, {
            headers: headers
        }).then((response) => response)
            .catch((error) => error)
    }

    function updateProductQuantity(productId, count) {

        return axios.put(`https://ecommerce.routemisr.com/api/v1/cart/${productId}`, {
            count: count

        }, {
            headers: headers
        }).then((response) => response)
            .catch((error) => error)
    }

    function onlinePayment(cartId, shippingAddress) {

        return axios.post(`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${cartId}`, {
            shippingAddress: shippingAddress

        }, {
            headers: headers
        }).then((response) => response)
            .catch((error) => error)
    }

    // get cart ID , number of cart items
    const [cartId, setCartId] = useState(null)
    const [numOfCartItems, setNumOfCartItems] = useState(0)

    async function getCart() {
        let response = await getLoggedUserCart()

        if (response?.data?.status === 'success') {

            setCartId(response?.data?.data?._id)
            setNumOfCartItems(response?.data?.numOfCartItems)

        }

    }

    const [numOfWishlistItems, setNumOfWishlistItems] = useState(0)

    async function getWishlist() {
        let response = await getLoggedUserWishlist()

        if (response?.data?.status === 'success') {

            setNumOfWishlistItems(response?.data?.count)

        }

    }

    useEffect(() => {
        getCart()
    }, [])
    
    useEffect(() => {
        getWishlist()
    }, [])

    return <CartContext.Provider value={{ addToWishlist, addToCart, getLoggedUserCart, removeCartItem, updateProductQuantity, clearCartItem, onlinePayment, cartId, numOfCartItems, setNumOfCartItems, numOfWishlistItems, setNumOfWishlistItems , getLoggedUserWishlist , removeWishlistItem }}>
        {props.children}
    </CartContext.Provider>
}


