import React, { useEffect, useState } from 'react';
import { jwtDecode } from 'jwt-decode';
import { Helmet } from 'react-helmet';

export default function Profile() {


    const [name, setName] = useState(null)

    useEffect(() => {

        let decodedToken = jwtDecode(localStorage.getItem('userToken'))

        setName(decodedToken.name)
    }, [])



    return <>

        <Helmet>
            <meta name='description' content='Home Page' charSet="utf-8" />
            <title>FreshCart Profile</title>
        </Helmet>

        <div className="container w-75 px-4 my-3 mx-auto p-4 bg-main-light mt-4">
            <h3 className='fw-bold mb-5'>User Profile:</h3>
            <div className="text-center">
                <h4 className='fw-bolder mb-3'>User Name :</h4>
                <h2 className='fw-bolder'>Hello <span className='text-main'>{name}</span></h2>
            </div>
        </div>


    </>
}
