import React from 'react';

export default function Footer() {
    return <>

        <div className="bg-main-light p-5 mt-5 container-fluid">
            <h3 className='fw-bold'>Get the FreshCart app</h3>
            <p className="text-muted">We will send you a link, open it on your phone to download app</p>

            <div className="d-flex justify-content-between w-100 border-bottom py-5 align-items-center">
                <input type="text" className='form-control w-50' placeholder='Email...' />
                <button className='col-sm-3 ms-md-2 btn bg-main text-white'>Share App Link</button>
            </div>

            <div className="d-flex justify-content-between border-bottom py-5 align-items-center">
                <div>
                    <span className='fw-bolder'>Payment Partners</span>
                    <i className=" ms-2 fa-lg fa-brands fa-amazon-pay"></i>
                    <i className=" ms-2 fa-lg fa-brands fa-cc-mastercard"></i>
                    <i className=" ms-2 fa-lg fa-brands fa-cc-paypal"></i>
                </div>
                <div>
                    <span className='fw-bolder'>Get deliveries with FreshCart</span>
                    <i className=" ms-2 fa-lg fa-brands fa-google-play "></i>
                    <i className=" ms-2 fa-lg fa-brands fa-apple "></i>
                </div>
            </div>

        </div>
    </>
}
