import React, { useState } from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import axios from 'axios';
import { Link, useNavigate } from 'react-router-dom';
import { FallingLines } from 'react-loader-spinner'
import { Helmet } from 'react-helmet';

export default function Register() {

    let navigate = useNavigate()
    const [error, setError] = useState(null)
    const [logging, setLogging] = useState(false)
    const phoneRegExp = /^01[0125]\d{8}$/

    async function submitRegister(value) {
        setLogging(true)
        let { data } = await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/signup`, value)
            .catch((err) => {
                setError(err.response.data.message)
                setLogging(false)
            })

        if (data.message === 'success') {
            setLogging(false)
            navigate('/Login')
        }

    }

    let validateSchema = Yup.object({
        name: Yup.string().min(3, 'name is minLength 3').max(10, 'name is maxLength 10').required('name is required'),
        email: Yup.string().email('email is invalid').required('email is required'),
        phone: Yup.string().matches(phoneRegExp, 'Phone is invalid').required('phone is required'),
        password: Yup.string().matches(/^[A-Z][a-z0-9]{5,10}$/, 'Password start with upperCase and minLength 6 , maxLength 10').required('password is required'),
        rePassword: Yup.string().oneOf([Yup.ref("password")], `password and rePassword don't match`).required('rePassword is required'),
    })

    let formik = useFormik({
        initialValues: {
            name: '',
            phone: '',
            email: '',
            password: '',
            rePassword: ''
        }, validationSchema: validateSchema,
        onSubmit: submitRegister
    })


    return <>

        <Helmet>
            <meta name='description' content='Home Page' charSet="utf-8" />
            <title>FreshCart Register</title>
        </Helmet>

        <div className="w-75 mx-auto py-4">

            <h3>Register Now</h3>

            <form onSubmit={formik.handleSubmit}>
                <label htmlFor="name">Name : </label>
                <input onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.name} id='name' name='name' type='text' className='form-control mb-2' />
                {formik.errors.name && formik.touched.name ? <div className="alert alert-danger p-2 mt-2 ">{formik.errors.name}</div> : ''}

                <label htmlFor="email">Email : </label>
                <input onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.email} id='email' name='email' type='email' className='form-control mb-2' />
                {formik.errors.email && formik.touched.email ? <div className="alert alert-danger p-2 mt-2 ">{formik.errors.email}</div> : ''}

                <label htmlFor="phone">Phone : </label>
                <input onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.phone} id='phone' name='phone' type='tel' className='form-control mb-2' />
                {formik.errors.phone && formik.touched.phone ? <div className="alert alert-danger p-2 mt-2 ">{formik.errors.phone}</div> : ''}

                <label htmlFor="password">Password : </label>
                <input onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.password} id='password' name='password' type='password' className='form-control mb-2' />
                {formik.errors.password && formik.touched.password ? <div className="alert alert-danger p-2 mt-2 ">{formik.errors.password}</div> : ''}

                <label htmlFor="rePassword">rePassword : </label>
                <input onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.rePassword} id='rePassword' name='rePassword' type='password' className='form-control mb-2' />
                {formik.errors.rePassword && formik.touched.rePassword ? <div className="alert alert-danger p-2 mt-2 ">{formik.errors.rePassword}</div> : ''}

                {logging ? <button type='button' className='btn mt-2'>

                    <FallingLines
                        width="50"
                        radius="9"
                        color="#4fa94d"
                        visible={true}
                        ariaLabel="falling-circles-loading"
                    />
                </button> :
                    <div>
                        <button disabled={!(formik.isValid && formik.dirty)} type='submit' className='btn bg-main text-white mt-2'>Register</button>
                        <Link to={'/login'}><button type='button' className='btn bg-info bg-opacity-75 text-white ms-2 mt-2'>Login Now</button></Link>
                    </div>
                }


                {error ? <div className="alert alert-danger p-2 mt-2 h5 text-center w-75 mx-auto">{error}</div> : ''}

            </form>
        </div>
    </>
}
