import React, { useContext } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import Logo from '../../Assets/images/freshcart-logo.svg'
import { UserContext } from '../../Context/UserContext.js';
import { CartContext } from '../../Context/CartContext.js';

export default function Navbar() {
    let { userToken, setUserToken, setUserData } = useContext(UserContext)
    let navigate = useNavigate()

    let { numOfCartItems, numOfWishlistItems } = useContext(CartContext)

    function logOut() {
        localStorage.removeItem('userToken');
        setUserToken(null)
        setUserData(null)
        navigate('/login')
    }


    return <>

        <nav className="navbar fixed-top navbar-expand-lg bg-body-tertiary">
            <div className="container-fluid">
                <Link className="navbar-brand" to="/">
                    <img src={Logo} alt="fresh market logo" />
                </Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        {userToken !== null ? <>
                            <li className="nav-item">
                                <Link className="nav-link" to="/freshCart">Home</Link>
                            </li>

                            <li className="nav-item">
                                <Link className="nav-link" to="/products">Products</Link>
                            </li>

                            <li className="nav-item">
                                <Link className="nav-link" to="/categories">Categories</Link>
                            </li>

                            <li className="nav-item">
                                <Link className="nav-link" to="/brands">Brands</Link>
                            </li>


                        </> : ''}

                    </ul>

                    <ul className="navbar-nav ms-auto mb-lg-0">

                        <li className="nav-item d-flex align-items-center">
                            <i className='fab fa-facebook mx-2'></i>
                            <i className='fab fa-twitter mx-2'></i>
                            <i className='fab fa-instagram mx-2'></i>
                            <i className='fab fa-tiktok mx-2'></i>
                            <i className='fab fa-youtube mx-2'></i>
                        </li>

                        {userToken !== null ? <>

                            <li className="nav-item position-relative ">
                                <Link className="nav-link" to="/wishlist">
                                    <i className='fas fa-heart text-main fa-lg pe-2'></i>
                                    <span className='position-absolute top-20 start-70 translate-middle badge rounded-pill text-white bg-danger'>{numOfWishlistItems}</span>
                                </Link>
                            </li>

                            <li className="nav-item position-relative">
                                <Link className="nav-link" to="/cart">
                                    <i className='fas fa-shopping-cart fa-lg pe-2'></i>
                                    <span className='position-absolute top-20 start-70 translate-middle badge rounded-pill bg-main text-white'>{numOfCartItems}</span>
                                </Link>
                            </li>

                            <li className="nav-item">
                                <Link className="nav-link" to="/profile">Profile</Link>
                            </li>

                            <li className="nav-item">
                                <span onClick={() => logOut()} className="nav-link cursor-pointer">Logout</span>
                            </li>

                        </> : <>
                            <li className="nav-item">
                                <Link className="nav-link" to="/register">Register</Link>
                            </li>

                            <li className="nav-item">
                                <Link className="nav-link" to="/login">Login</Link>
                            </li>
                        </>}

                    </ul>

                </div>
            </div>
        </nav>


    </>
}
