import React, { useContext } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import { useQuery } from 'react-query';
import Slider from "react-slick";
import { BallTriangle } from 'react-loader-spinner';
import { Helmet } from 'react-helmet';
import { CartContext } from '../../Context/CartContext.js';
import toast from 'react-hot-toast';

export default function ProductDetails() {

    let params = useParams();

    function getProductDetails(id) {
        return axios.get(`https://ecommerce.routemisr.com/api/v1/products/${id}`)
    }

    let { isLoading, data } = useQuery('productDetails', () => getProductDetails(params.id), {
        cacheTime: 9000000
    });

    let product = data?.data.data


    let { addToCart, setNumOfCartItems, addToWishlist, setNumOfWishlistItems } = useContext(CartContext)

    async function addProductToCart(productId, name) {

        let response = await addToCart(productId)

        if (response?.data?.status === 'success') {

            setNumOfCartItems(response?.data.numOfCartItems)

            toast.success(`${name} added To cart`)
        } else {
            toast.error(`${name} Error adding product`)
        }
    }

    async function addProductToWishlist(productId, name) {

        let response = await addToWishlist(productId)

        if (response?.data?.status === 'success') {
            setNumOfWishlistItems(response?.data?.data?.length)

            toast.success(`${name} added To Whish List`)
        } else {
            toast.error(`${name} Error adding product`)
        }
    }

    let settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 3000,
    };

    return <>

        {isLoading ?
            <div key={params.id} className="w-100 vh-100 py-5 d-flex justify-content-center align-items-center">
                <BallTriangle
                    height={200}
                    width={100}
                    radius={5}
                    color="#4fa94d"
                    ariaLabel="ball-triangle-loading"
                    wrapperStyle={{}}
                    wrapperClass=""
                    visible={true}
                />
            </div>

            :

            product ? <div className="row my-2 py-2 align-items-center justify-content-center">

                <Helmet>
                    <meta name='description' content='product details' charSet="utf-8" />
                    <title>{product.title}</title>
                </Helmet>

                <div className="col-md-3">

                    <Slider {...settings}>

                        {product.images.map((image) => <img key={product._id} src={image} alt={product.title} className='w-100' />)}

                    </Slider>

                </div>
                <div className="col-md-8 mt-4">

                    <div className="d-flex justify-content-start align-items-center mb-1">
                        <h2 className='h5 fw-bolder'>{product.title}</h2>
                        <i onClick={() => addProductToWishlist(product._id, product.title.split(' ').slice(0, 2).join(' '))} className='fas fa-heart p-2 ms-2 fa-2x hover-heart border bg-custom cursor-pointer'></i>
                    </div>

                    <p className='text-muted'>{product.description}</p>

                    <h6 className='fw-bold'>{product.category.name}</h6>

                    <div className="d-flex justify-content-between">
                        <span className='text-muted fw-bolder'>{product.price} EGP</span>
                        <span>
                            <i className="fas fa-star rating-color"></i>
                            {product.ratingsAverage}
                        </span>
                    </div>

                    <button onClick={() => addProductToCart(product._id, product.title.split(' ').slice(0, 2).join(' '))} className='btn bg-main text-white w-100 mt-2'>+ add to cart</button>

                </div>
            </div >
                :
                ''
        }


    </>
}
