import React, { useContext } from 'react';
import { BallTriangle } from 'react-loader-spinner';
import { CartContext } from '../../Context/CartContext.js';
import { useQuery } from 'react-query';
import axios from 'axios';
import toast from 'react-hot-toast';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';

export default function Products() {

    function getAllProducts() {

        return axios.get(`https://ecommerce.routemisr.com/api/v1/products?limit=50`)
    }

    let { isLoading, data } = useQuery('AllProducts', getAllProducts, {
        cacheTime: 9000000
    })

    let { addToCart, setNumOfCartItems, addToWishlist, setNumOfWishlistItems } = useContext(CartContext)

    async function addProductToCart(productId, name) {

        let response = await addToCart(productId)

        if (response?.data?.status === 'success') {
            setNumOfCartItems(response?.data.numOfCartItems)

            toast.success(`${name} added To cart`)
        } else {
            toast.error(`${name} Error adding product`)
        }
    }

    async function addProductToWishlist(productId, name) {

        let response = await addToWishlist(productId)

        if (response?.data?.status === 'success') {
            setNumOfWishlistItems(response?.data?.data?.length)

            toast.success(`${name} added To Whish List`)
        } else {
            toast.error(`${name} Error adding product`)
        }
    }

    return <>

        <Helmet>
            <meta name='description' content='Home Page' charSet="utf-8" />
            <title>FreshCart Products</title>
        </Helmet>

        {isLoading ?
            <div className="w-100 vh-100 py-5 d-flex justify-content-center align-items-center">
                <BallTriangle
                    height={200}
                    width={100}
                    radius={5}
                    color="#4fa94d"
                    ariaLabel="ball-triangle-loading"
                    wrapperStyle={{}}
                    wrapperClass=""
                    visible={true}
                />
            </div>

            :

            <div className="row my-4 mx-0">
                {data?.data?.data.map((product) => <div key={product._id} className="col-md-2">

                    <div className="product cursor-pointer px-2 py-3 position-relative">

                        <div className="hover-heart border bg-custom position-absolute end-0 top-0 me-2 mt-2">
                            <i onClick={() => addProductToWishlist(product._id, product.title.split(' ').slice(0, 2).join(' '))} className='fas fa-heart fa-xl'></i>
                        </div>

                        <Link to={`/productdetails/${product._id}`}>

                            <img src={product.imageCover} alt={product.title} className='w-100' />

                            <span className='text-main fw-bold font-sm'>{product.category.name}</span>
                            <h3 className='h6 fw-bolder'>{product.title.split(' ').slice(0, 2).join(' ')}</h3>
                            <div className="d-flex justify-content-between">
                                <span className='text-muted fw-bold'>{product.price} EGP</span>
                                <span>
                                    <i className="fas fa-star rating-color"></i>
                                    {product.ratingsAverage}
                                </span>
                            </div>



                        </Link>

                        <button onClick={() => addProductToCart(product._id, product.title.split(' ').slice(0, 2).join(' '))} className='btn bg-main text-white w-100 mt-2'>
                            + add to cart
                            <i className='fas fa-shopping-cart fa-1x ms-2'></i>
                        </button>
                    </div>

                </div>)}

            </div >

        }

    </>
}
