import React, { useContext, useState } from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { CartContext } from '../../Context/CartContext.js';
import { FallingLines } from 'react-loader-spinner';
import toast from 'react-hot-toast';

export default function CheckOut() {

    const [error, setError] = useState(null)
    const [logging, setLogging] = useState(false)

    let { onlinePayment, cartId } = useContext(CartContext)

    async function handleCheckOut(values) {
        setLogging(true)

        let response = await onlinePayment(cartId, values)

        if (response?.data?.status === 'success') {
            setLogging(false)
            window.location.href = response?.data?.session?.url

        } else if (response?.response?.data?.statusMsg === 'fail') {
            setLogging(false)
            setError('There is no cart')
        } else {
            setLogging(false)
            setError('There is no cart')
        }
        console.log(response.data.status);
    }

    const phoneRegExp = /^01[0125]\d{8}$/

    let validateSchema = Yup.object({
        details: Yup.string().min(3, 'Details is minLength 3').max(60, 'Details is maxLength 60').required('Details is required'),
        phone: Yup.string().matches(phoneRegExp, 'Phone is invalid').required('Phone is required'),
        city: Yup.string().min(3, 'City is minLength 3').max(20, 'City is maxLength 20').required('City is required'),
        postalCode: Yup.string().min(5, 'Postal Code is Length 5').max(5, 'Postal Code is Length 5').required('Postal Code is required'),
    })

    let formik = useFormik({
        initialValues: {
            details: '',
            phone: '',
            city: '',
            postalCode: ''
        }, validationSchema: validateSchema,
        onSubmit: handleCheckOut
    })

    function okCashOrder() {
        return toast.success("Cash Order Done")
    }

    return <>

        <div className="col-md-9 px-1 mx-auto py-4">

            <h3>Check Out</h3>

            <form onSubmit={formik.handleSubmit} className='mt-4'>

                <label className='my-1' htmlFor="details">Details : </label>
                <textarea onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.details} id='details' name='details' type='text' className='form-control mb-2' />
                {formik.errors.details && formik.touched.details ? <div className="alert alert-danger p-2 mt-2 ">{formik.errors.details}</div> : ''}

                <label className='my-1' htmlFor="phone">Phone : </label>
                <input onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.phone} id='phone' name='phone' type='tel' className='form-control mb-2' />
                {formik.errors.phone && formik.touched.phone ? <div className="alert alert-danger p-2 mt-2 ">{formik.errors.phone}</div> : ''}

                <label className='my-1' htmlFor="city">City : </label>
                <input onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.city} id='city' name='city' type='text' className='form-control mb-2' />
                {formik.errors.city && formik.touched.city ? <div className="alert alert-danger p-2 mt-2 ">{formik.errors.city}</div> : ''}

                <label className='my-1' htmlFor="postalCode">Postal Code : </label>
                <input onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.postalCode} id='postalCode' name='postalCode' type='number' className='form-control mb-2' />
                {formik.errors.postalCode && formik.touched.postalCode ? <div className="alert alert-danger p-2 mt-2 ">{formik.errors.postalCode}</div> : ''}

                {logging ? <button type='button' className='btn mt-2'>

                    <FallingLines
                        width="50"
                        radius="9"
                        color="#4fa94d"
                        visible={true}
                        ariaLabel="falling-circles-loading"
                    />
                </button> :
                    <div className='d-flex justify-content-between col-md-10 mx-auto'>
                        <button disabled={!(formik.isValid && formik.dirty)} type='submit' className='btn bg-main text-white mt-3 me-2 col-md-4'>
                            <i className="fa-regular fa-credit-card me-2"></i>
                            Online Payment
                        </button>

                        <button disabled={!(formik.isValid && formik.dirty)} onClick={() => okCashOrder() && formik.resetForm({ details: '', phone: '', city: '', postalCode: '' })} type='button' className='btn bg-main text-white mt-3 col-md-4'>
                            <i className="fa-solid fa-hand-holding-dollar me-2"></i>
                            Cash on Delivery
                        </button>
                    </div>
                }

                {error ? <div className="alert alert-danger p-2 mt-2 h5 text-center w-75 mx-auto">{error}</div> : ''}

            </form>
        </div>
    </>


}
