import React from 'react';
import { BallTriangle } from 'react-loader-spinner';
import axios from 'axios';
import { useQuery } from 'react-query';
import { Helmet } from 'react-helmet';

export default function Brands() {
    function getAllBrands() {

        return axios.get(`https://ecommerce.routemisr.com/api/v1/brands?limit=50`)
    }

    let { isLoading, data } = useQuery('AllBrands', getAllBrands, {
        cacheTime: 9000000
    })


    return <>

        <Helmet>
            <meta name='description' content='Home Page' charSet="utf-8" />
            <title>FreshCart Brands</title>
        </Helmet>

        {isLoading ?
            <div className="w-100 vh-100 py-5 d-flex justify-content-center align-items-center">
                <BallTriangle
                    height={200}
                    width={100}
                    radius={5}
                    color="#4fa94d"
                    ariaLabel="ball-triangle-loading"
                    wrapperStyle={{}}
                    wrapperClass=""
                    visible={true}
                />
            </div>

            :

            <div className="row my-4 mx-0">
                {data?.data?.data.map((brand) => <div key={brand._id} className="col-md-2">

                    <div className="product px-2 py-3 text-center">

                        <img src={brand.image} height={150} alt={brand.name} className='w-100' />

                        <span className='text-main fw-bold font-sm'>{brand.name}</span>
                    </div>

                </div>)}

            </div >

        }

    </>
}
