import React from 'react';
import Slider from 'react-slick';
import { useQuery } from 'react-query';
import axios from 'axios';

export default function CategorySlider() {

    function getCategorySlider() {

        return axios.get(`https://ecommerce.routemisr.com/api/v1/categories`)
    }

    let { data } = useQuery('CategorySlider', getCategorySlider, {
        cacheTime: 9000000
    })

    let categories = data?.data.data;

    let settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 3,
        autoplay: true,
        autoplaySpeed: 3000,
    };

    return <>

        {categories ? <div className="row col-md-11 py-4 mx-auto">

            <Slider className='col-md-11 mx-auto' {...settings}>

                {categories.map((category) => <div key={category._id} className="text-center">

                    <img src={category.image} alt={category.name} height={210} className='w-100' />
                    <h2 className='h6'>{category.name}</h2>
                </div>)}

            </Slider>

        </div>

            : ''
        }


    </>
}
